
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'Title',
    computed: {
        companyName() {
            return this.$store.state.companyName;
        },
        companySlogan() {
            return this.$store.state.companySlogan;
        },
    },
    methods: {
        redirectToHomePage() {
            this.$router.push({ name: 'HomePage' });
        },
    },
});
